import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GetPhotoGalleryService } from './photoGalleryService';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-photo-gallery',
  templateUrl: './photo-gallery.page.html',
  styleUrls: ['./photo-gallery.page.scss'],
})
export class PhotoGalleryPage implements OnInit {

  loaded: boolean = false;
  pageNum: number = 1;
  isLastPageReached: boolean = false;
  photosList = [];
  errorMsg: any;

  constructor(private router: Router, public getPhotoGalleryService: GetPhotoGalleryService, public utils: UtilService, public translate: TranslateService) { }

  ngOnInit() {
    this.getPhotoGalleryList(undefined);
  }

  /**
   * Method used to repalce 'bpp1.' with ''
   * @param link - Image Url that need to be changed
   */
  replaceURL(link: any) {
    // if (link != null && link != undefined) {
    //   link = link.replace('bpp1.', '');
    return link;
    // }
  }

  navToDetails(photoID) {
    this.router.navigate(['/photo-gallery-details'], { state: { photoID: photoID } })
  }

  getPhotoGalleryList(event) {
    this.utils.showProgressLoader();
    this.getPhotoGalleryService.getPhotoGallery(this.pageNum).then(
      (result) => {
        try {
          this.utils.dissmisProgressLoader();
          if (this.photosList.length > 0) {
            this.photosList = this.photosList.concat(result['responseJSON']['array']);
          } else {
            this.photosList = result['responseJSON']['array'];
            ConfigProvider.PAGE_SIZE = result['responseJSON']['pageSize'];
          }
          if (!(this.photosList.length > 0)) {
            if (result['responseJSON']['array'].length < ConfigProvider.PAGE_SIZE || result['responseJSON']['array'].length === 0) {
              this.isLastPageReached = true;
            } else {
              this.pageNum++;
            }
          } else {
            if (result['responseJSON']['array'].length < ConfigProvider.PAGE_SIZE || result['responseJSON']['array'].length === 0) {
              this.isLastPageReached = true;
            } else {
              this.pageNum++;
            }
          }
        } catch (e) {
          this.utils.dissmisProgressLoader();
          this.errorMsg = this.translate.instant('generic_msgs.server_err');
        }
      },
      (err) => {
        this.utils.dissmisProgressLoader();
        this.errorMsg = this.utils.handleServiceException(err);
      }
    );
  }

  getMorePhotos(event) {
    this.getPhotoGalleryList(event);
  }

}
